.playlist{
  margin: 20px;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.bienvenida{
  padding-top: 20px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(73, 84, 69)), color-stop(85%, rgb(7, 8, 6)));
  background-image: -webkit-linear-gradient(rgb(73, 84, 69), rgb(7, 8, 6) 85%);
  background-image: -o-linear-gradient(rgb(73, 84, 69), rgb(7, 8, 6) 85%);
  background-image: linear-gradient(rgb(73, 84, 69), rgb(7, 8, 6) 85%);
  color: #fff;
}

.main-nav li {
  display: inline-block;
}

.main-nav a {
  font-size: 1em;
  color: #a70707;
  padding: .2em .8em;
  border-radius: .25em;
  background: #eaebeb;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  margin-left: 10px;
  text-decoration: none;
}

.main-nav a.active, .main-nav a:hover {
  color: #fff;
  background: #39add1;
}
.title{
  color: #fff;
}



