.play {
  margin: 50px;
}

.init {
  padding-top: 20px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#436181), color-stop(45%, #140806));
  background-image: -webkit-linear-gradient(#436181, #140806 45%);
  background-image: -o-linear-gradient(#436181, #140806 45%);
  background-image: linear-gradient(#436181, #140806 45%);
  color: #fff;
}

.audio {
  width: 150px;
}

.title {
  width: 150px;
}

.news {
  margin-right: 510px;
  margin-left: 510px;
}

.link {
  margin-right: 450px;
  margin-left: 450px;
}

.opacity:hover {
  opacity: 0.5;
  filter: alpha(opacity=50);
  /* For IE8 and earlier */
}


